<template>
  <div class="hello">
    <vc-layout>
      <vc-col :span="18" class="m-auto">
        <h3 :class="['headline', 'ma-0', 'pa-0', 'ellipsis']">
          <vc-badge color="blue-grey l-900">2</vc-badge> Crea tu usuario
        </h3>
        <br><br>
        <vc-tabstrip name="tabstrip-0" color="blue-grey l-600" slider-color="orange">
          <vc-tabstrip-pane label="Requerido">
            <vc-text type="text" v-model="user.email" label="Email" icon-left="mdi-at" name="user-email" color="primary" required />
            <vc-text type="text" v-model="user.firstname" label="Nombres" icon-left="mdi-account-check" name="user-firstname" color="primary" required />
            <vc-text type="text" v-model="user.lastname" label="Apellidos" icon-left="mdi-account-check" name="user-lastname" color="primary" required />
            <vc-text type="number" v-model="user.celphone" label="Celular" icon-left="mdi-cellphone-iphone" name="user-celphone" color="primary" required />
          </vc-tabstrip-pane>
          <vc-tabstrip-pane label="Opcional">
            <vc-text type="number" v-model="user.phone" label="Teléfono fijo" icon-left="mdi-phone-classic" name="user-phone" color="primary" />
            <vc-text type="text" v-model="user.address" label="Dirección" icon-left="mdi-home-map-marker" name="user-address" color="primary" />
            <vc-text type="text" v-model="user.neighbourhood" label="Barrio" icon-left="navigate_next" name="user-neighbourhood" color="primary" />
            <vc-text type="text" v-model="user.city" label="Ciudad" icon-left="mdi-city" name="user-city" color="primary" />
            <vc-text type="text" v-model="user.province" label="Provincia" icon-left="add_location" name="user-province" color="primary" />
          </vc-tabstrip-pane>
        </vc-tabstrip>
        <br><br>
        <vc-button color="success" icon-right="check" class="pull-right" :disabled="disableButton" @click="sendData">Crear</vc-button>
        <vc-loading-overlay
          application
          :dark="true"
          :visible="visible"
          spinnerSize="x-large"
          spinnerIcon="mdi-loading"
          spinnerColor="info"
        ></vc-loading-overlay>
        <vc-notification
          @added="onAdded($event)"
          @removed="onRemoved($event)"
          vertical="top"
          horizontal="right"
          name="notificationUser" />
      </vc-col>
    </vc-layout>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'FormUser',
  data () {
    return {
      user: {
        email: '',
        firstname: '',
        lastname: '',
        phone: '',
        celphone: '',
        address: '',
        neighbourhood: '',
        city: '',
        province: ''
      },
      visible: false
    }
  },
  computed: {
    disableButton () {
      return !this.user.email || !this.user.firstname || !this.user.lastname || !this.user.celphone
    }
  },
  methods: {
    sendData () {
      if (!this.disableButton) {
        this.visible = true
        this.createUser(this.user)
          .then(res => {
            this.visible = false
            this.notify('Usuario creado', 'success', 'notificationUser')
          })
          .catch(err => {
            this.visible = false
            this.notify('Ha ocurrido un error', 'error', 'notificationUser')
            console.error(err)
          })
      }
    },
    onAdded (data) {
      console.log('vc-notification:added', data)
    },
    onRemoved (data) {
      console.log('vc-notification:removed', data)
    },
    notify (msg, type, name) {
      this.$root.$emit('vc:notification', {
        icon: 'notifications_active',
        text: msg,
        delay: 5000,
        color: type,
        dismissable: true,
        dark: true,
        name: name
      })
      this.$nextTick(() => {
        this.notificationNumber++
      })
    },
    ...mapActions(['createUser'])
  }
}
</script>

