<template>
  <div class="hello">
    <vc-layout>
      <vc-col :span="15" class="m-auto">
        <h3 :class="['headline', 'ma-0', 'pa-0', 'ellipsis']">
          <vc-badge color="blue-grey l-900">1</vc-badge> Configura tu dispositivo
        </h3>
        <br><br>
        <vc-text type="number" v-model="device.deviceId" label="Número del dispositivo" icon-left="mdi-webcam" name="device-number" color="primary" required />
        <vc-text type="text" v-model="device.deviceName" label="Nombre del dispositivo" icon-left="mdi-rename-box" name="device-name" color="primary" required />
        <vc-text type="password" v-model="device.password" label="Password del dispositivo" icon-left="vpn_key" name="device-password" color="primary" />
        <br><br><br>
        <vc-button color="success" icon-right="check" class="pull-right" :disabled="disableButton" @click="sendData">Suscribirse</vc-button>
        <vc-loading-overlay
          application
          :dark="true"
          :visible="visible"
          spinnerSize="x-large"
          spinnerIcon="mdi-loading"
          spinnerColor="info"
        ></vc-loading-overlay>
        <vc-notification
          @added="onAdded($event)"
          @removed="onRemoved($event)"
          vertical="top"
          horizontal="right"
          name="notificationDevice" />
      </vc-col>
    </vc-layout>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'FormDevice',
  data () {
    return {
      device: {
        deviceId: undefined,
        deviceName: '',
        password: ''
      },
      visible: false
    }
  },
  computed: {
    disableButton () {
      return !this.device.deviceId || !this.device.deviceName
    }
  },
  methods: {
    sendData () {
      if (!this.disableButton) {
        this.visible = true
        this.device.deviceId = this.device.deviceId.toString()
        this.createDevice(this.device)
          .then(res => {
            this.visible = false
            this.notify('Dispositivo generado', 'success', 'notificationDevice')
          })
          .catch(err => {
            this.visible = false
            this.notify('Ha ocurrido un error', 'error', 'notificationDevice')
            console.error(err)
          })
      }
    },
    onAdded (data) {
      console.log('vc-notification:added', data)
    },
    onRemoved (data) {
      console.log('vc-notification:removed', data)
    },
    notify (msg, type, name) {
      this.$root.$emit('vc:notification', {
        icon: 'notifications_active',
        text: msg,
        delay: 5000,
        color: type,
        dismissable: true,
        dark: true,
        name: name
      })
      this.$nextTick(() => {
        this.notificationNumber++
      })
    },
    ...mapActions(['createDevice'])
  }
}
</script>

