<template>
  <div class="hello">
    <vc-layout>
      <vc-col :span="15" class="m-auto">
        <h3 :class="['headline', 'ma-0', 'pa-0', 'ellipsis']">
          <vc-badge color="blue-grey l-900">3</vc-badge> Suscríbete al dispositivo
        </h3>
        <br><br>
        <vc-select
          searchable
          color="primary"
          label="Usuarios por email"
          icon="mdi-at"
          name="users-emails"
          @input="setEmailAndTarget"
          v-model="selectedEmail"
          :items="emailsItems">
        </vc-select>
        <vc-select
          searchable
          color="primary"
          label="Dispositivos por id"
          icon="mdi-webcam"
          name="device-ids"
          @input="setDeviceId"
          v-model="selectedDevice"
          :items="devicesItems">
        </vc-select>
        <vc-text type="text" v-model="subscription.deviceName" label="Nombre descriptivo para el mail" icon-left="keyboard_arrow_right" name="subscription-deviceName" color="primary" required />
        <vc-text type="number" v-model="subscription.limit" label="Limite de notificaciones" icon-left="vertical_align_top" name="subscription-deviceName" color="primary" />
        Email <vc-switcher color="primary" id="subscription-target" name="subscription-target" v-model="type">SMS</vc-switcher>
        <vc-text v-if="!type" type="email" v-model="subscription.target" label="Email al que desea notificar" icon-left="mdi-target" name="subscription-target" color="primary" required />
        <vc-text v-if="type" type="text" v-model="subscription.target" label="Numero del celular" icon-left="mdi-target" name="subscription-target2" color="primary" required />
        <br><br><br>
        <vc-button color="success" icon-right="check" class="pull-right" :disabled="disableButton" @click="sendData">Cargar</vc-button>
        <vc-loading-overlay
          application
          :dark="true"
          :visible="visible"
          spinnerSize="x-large"
          spinnerIcon="mdi-loading"
          spinnerColor="info"
        ></vc-loading-overlay>
        <vc-notification
          @added="onAdded($event)"
          @removed="onRemoved($event)"
          vertical="top"
          horizontal="right"
          name="notificationSubscription" />
      </vc-col>
    </vc-layout>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  name: 'FormSubscription',
  data () {
    return {
      subscription: {
        userEmail: '',
        deviceId: undefined,
        deviceName: '',
        limit: 10,
        type: 'email',
        target: ''
      },
      type: false,
      emailsItems: [],
      devicesItems: [],
      selectedEmail: [],
      selectedDevice: [],
      visible: false,
      notiUser: 0,
      notiDevice: 0,
      trashTarget: ''
    }
  },
  computed: {
    disableButton () {
      return (!this.subscription.userEmail || !this.subscription.deviceId || !this.subscription.deviceName || !this.subscription.target || !this.subscription.type || !this.subscription.limit)
    },
    ...mapState({
      users: state => state.users,
      devices: state => state.devices
    })
  },
  methods: {
    setEmailAndTarget () {
      this.subscription.userEmail = this.selectedEmail[0].value
      this.subscription.target = this.selectedEmail[0].value
    },
    setDeviceId () {
      this.subscription.deviceId = this.selectedDevice[0].value
    },
    sendData () {
      if (!this.disableButton) {
        this.visible = true
        this.subscription.target = '+' + this.subscription.target
        this.createSubscription(this.subscription)
          .then(res => {
            this.visible = false
            this.notify('Subscripción realizada', 'success', 'notificationSubscription')
          })
          .catch(err => {
            this.visible = false
            this.notify('Ha ocurrido un error', 'error', 'notificationSubscription')
            console.error(err)
          })
      }
    },
    onAdded (data) {
      console.log('vc-notification:added', data)
    },
    onRemoved (data) {
      console.log('vc-notification:removed', data)
    },
    notify (msg, type, name) {
      this.$root.$emit('vc:notification', {
        icon: 'notifications_active',
        text: msg,
        delay: 10000,
        color: type,
        dismissable: true,
        dark: true,
        name: name
      })
      this.$nextTick(() => {
        this.notificationNumber++
      })
    },
    updateInfo () {
      this.visible = true
      this.listUsers()
        .then(res => this.listDevices())
        .then(res => {
          if (!this.users.length) {
            this.notify('No hay usuarios, por favor primero genere un usuario', 'error', 'notificationSubscription')
            this.notiUser++
          }
          if (!this.devices.length) {
            this.notify('No hay dispositivos, por favor primero cree un dispositivo', 'error', 'notificationSubscription')
            this.notiDevice++
          }
          this.users.forEach(el => {
            this.emailsItems.push({label: el.email, value: el.email})
          })
          this.devices.forEach(el => {
            this.devicesItems.push({label: el.deviceName, value: el.deviceId})
          })
          this.visible = false
        })
        .catch(err => {
          console.log(err)
          this.visible = false
        })
    },
    ...mapActions(['listUsers', 'listDevices', 'createSubscription'])
  },
  watch: {
    type (newType, oldType) {
      if (newType) {
        this.subscription.type = 'sms'
      } else {
        this.subscription.type = 'email'
      }

      let temp = this.trashTarget
      this.trashTarget = this.subscription.target
      this.subscription.target = temp
    }
  },
  mounted () {
    window.EventBus.$on('tabSubscriptions', (payload) => {
      this.updateInfo()
    })
    this.updateInfo()
  }
}
</script>

